import React, {useEffect, useState} from "react";
import styles from "../newRelease/releases.module.scss";
import {Filter} from "../../common/filterPanel/Filter";
import {MovieItem} from "../../common/movieItem/MovieItem";
import {useDispatch, useSelector} from "react-redux";
import {FetchMovieAC, FetchTrendingAC, movieItemType, setMovieAC, setTrendingMovieAC} from "../../redux/movie-reducer";
import { getRendingMovie } from "../../redux/selectors";

export const Trending = () => {
    const trendingMovie = useSelector(getRendingMovie)
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(FetchTrendingAC())
    }, [dispatch])
    const genreHandleChange = (event: React.ChangeEvent<{ value: any }>) => arrOfId.map((el) => el.name === event.target.value ? setId(el.id) : '')
    const [id, setId] = useState<number | null>(null)
    const filteredArr = trendingMovie.filter(el => id !== null? el.genre_ids.includes(id) : el)
    const filterRatingHandler = () => dispatch(setTrendingMovieAC(filterByRating(trendingMovie)))
    const filterTitleHandler = () => dispatch(setTrendingMovieAC(filterByName(trendingMovie)))
    let filterByRating = (movie: Array<movieItemType>) => trendingMovie.slice().sort(function (a,b){return a.vote_average - b.vote_average}).reverse()
    let filterByName = (movie: Array<movieItemType>) => trendingMovie.slice().sort((a,b) => b.original_title.toLowerCase().localeCompare(a.original_title.toLowerCase()))
    const arrOfId = [
        {id: 28, name: "Action"},
        {id: 12, name: "Adventure"},
        {id: 16, name: "Animation"},
        {id: 35, name: "Comedy"},
        {id: 80, name: "Crime"},
        {id: 99, name: "Documentary"},
        {id: 18, name: "Drama"},
        {id: 10751, name: "Family"},
        {id: 14, name: "Fantasy"},
        {id: 36, name: "History"},
        {id: 27, name: "Horror"},
        {id: 10402, name: "Music"},
        {id: 9648, name: "Mystery"},
        {id: 10749, name: "Romance"},
        {id: 878, name: "Science Fiction"},
        {id: 10770, name: "TV Movie"},
        {id: 53, name: "Thriller"},
        {id: 10752, name: "War"},
        {id: 37, name: "Western"}
    ]
    return(
        <div className={styles.releases}>
            <div className={styles.mainTitle}>
                <h2>Trending</h2>
                <h3>Trending</h3>
            </div>
            <Filter filterRatingHandler={filterRatingHandler} filterTitleHandler={filterTitleHandler} genreHandleChange={genreHandleChange} arrOfId={arrOfId}/>
            <div className={styles.releasesInner}>
                {filteredArr.length === 0 && <div>No results</div>}
                {filteredArr.map((film) => <MovieItem key={film.id} film={film}/>)}
            </div>
        </div>
    )
}