import React, {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import { MovieItem } from "../../common/movieItem/MovieItem";
import {movieItemType, resentCountAC } from "../../redux/movie-reducer";
import {getWatchlist} from "../../redux/selectors";
import styles from './watchlist.module.scss'

export const Watchlist = () => {
    const watchList = useSelector(getWatchlist)


    let cities = watchList.reduce((acc, city) => {
        if (acc.map[city.id]) // если данный город уже был
            return acc; // ничего не делаем, возвращаем уже собранное

        acc.map[city.id] = true// помечаем город, как обработанный
        acc.cities.push(city); // добавляем объект в массив городов
        return acc; // возвращаем собранное
    }, {
        map: {} as any, // здесь будут отмечаться обработанные города
        cities: [] as Array<movieItemType> // здесь конечный массив уникальных городов
    })
        .cities; // получаем конечный массив
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(resentCountAC())
    })
    return (
        <div className={styles.watchlistWr}>
            {cities.map((film) => <MovieItem key={film.id} film={film}/>)}
        </div>
    )
}