import {put, call, takeEvery} from 'redux-saga/effects'
import { movieApi } from '../../api/api'
import {FETCH_DETAILS, FETCH_UPCOMING_MOVIE, setMovieAC, setMovieDetailsAC, FETCH_SEARCH_MOVIE, setSearchMovieAC, FETCH_TRAILER, setTrailerAC, ChangeIsFetchingValueAC, FETCH_TRENDING_MOVIE, setTrendingMovieAC, FETCH_RATED_MOVIE, setRatedAC} from "../movie-reducer";

function* fetchUpcomingMovie(action: any) {
    yield put(ChangeIsFetchingValueAC(true))
    // @ts-ignore
    let res = yield call(() => movieApi.getMovies(action.movieId))
    yield put(setMovieAC(res.data.results))
    yield put(ChangeIsFetchingValueAC(false))
}

function* fetchTrendingMovie() {
    // @ts-ignore
    let res = yield call(() => movieApi.getTrending())
    yield put(setTrendingMovieAC(res.data.results))
}
function* fetchRatedMovie() {
    yield put(ChangeIsFetchingValueAC(true))
    // @ts-ignore
    let res = yield call(() => movieApi.getTopRated())
    yield put(ChangeIsFetchingValueAC(false))
    yield put(setRatedAC(res.data.results))
}
function* fetchMovieDetails(action: any){
    yield put(ChangeIsFetchingValueAC(true))
    // @ts-ignore
    let res = yield call(() => movieApi.getMovieDetails(action.movieId))
    yield put(setMovieDetailsAC(res.data))
    yield put(ChangeIsFetchingValueAC(false))
}

function* fetchSearchMovie(action: any){
    // @ts-ignore
    let res = yield call(() => movieApi.getMovieResult(action.value))
    yield put(setSearchMovieAC(res.data.results))
}
function* fetchTrailer(action: any){
    yield put(ChangeIsFetchingValueAC(true))
    // @ts-ignore
    let res = yield call(() => movieApi.getMovieTrailer(action.movieId))
    yield put(ChangeIsFetchingValueAC(false))
    yield put(setTrailerAC(res.data.results))
}

export function* requestMovieWatcher(){
    yield takeEvery(FETCH_UPCOMING_MOVIE, fetchUpcomingMovie)
    yield takeEvery(FETCH_DETAILS, fetchMovieDetails)
    yield takeEvery(FETCH_SEARCH_MOVIE, fetchSearchMovie)
    yield takeEvery(FETCH_TRAILER, fetchTrailer)
    yield takeEvery(FETCH_TRENDING_MOVIE, fetchTrendingMovie)
    yield takeEvery(FETCH_RATED_MOVIE, fetchRatedMovie)
}